* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.hide {
  display: none;
}

.HomeContainer {
  position: fixed;
  top: 8vh;
  width: 100%;
  height: 92vh;
}

.HomeMainContainer {
  position: fixed;
  width: 80vw;
  height: 92vh;
  top: 8vh;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  justify-content: center;
}

.bg-img {
  position: relative;
  width: 80vw;
  height: 100%;
  background: url('../public/homecover.jpeg') no-repeat;
  background-size: cover;
  box-shadow: 0 2px 2px -1px rgba(0, 0, 0, 0.2);
}

.bg-img h1 {
  padding: 0;
  margin-top: 10vh;
  color: #eee;
  width: 35%;
  margin-left: 30px;
  text-align: left;
  font-weight:bold;
  font-size: 2.5rem;
}

.HomeTextBox {
  position: relative;
  height: 100%;
  width: 80vw;
  padding: 30px;
  box-shadow: 0px 10px 10px rgba(0,0,0, 0.2);
}

.HomeTextBox h2 {
  color: rgba(0, 0, 0, 0.5);
  width: 100%;
  font-size: 1.2rem;
  font-weight: 100;
  text-align: justify;
  line-height: 1.7rem;
}

.HomeMainContainer a {
  text-decoration: none;
  background-color: #eee;
  padding: 0 7px;
  border-radius: 10px;
  color: #F19F39;
  transition: all ease-in-out 0.3s;
}

.HomeMainContainer a:hover {
  background-color: #F19F39;
  color: #eee;
  transition: all ease-in-out 0.3s;
}

.TwitterFeed {
  position: fixed;
  height: 92vh;
  width: 20vw;
  top: 8vh;
  overflow-y: scroll;
  right: 0;
  box-shadow: -5px 1px 4px -2px rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 960px){
  .HomeContainer {
    position: fixed;
    overflow-y: scroll;
    padding: 0;
  }

  .bg-img {
    background: none;
    width: 100%;
  }

  .bg-img h1 {
    color: rgba(0, 0, 0, 0.5);
    margin-top: 0;
    width: 80%;
    font-size: 1.5rem;
  }

  .HomeMainContainer {
    position: relative;
    height: 100%;
    width: 100vw;
  }

  .TwitterFeed {
    position: relative;
  }
}